<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>1. 保單契約名稱 ?</h3>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
            <div>
                <input type="text" v-model.trim="msg" class="form-control" placeholder="輸入保單契約名稱">
                <br>
                <button @click="press" class="btn btn-outline-success" type="button">
                    <b>確認</b>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data (){
        return {
            msg: ''
        }
    },
    methods: {
        press: function(){
            if(this.msg.length == 0){
                alert("請填寫保單契約名稱");
            }
            else{
                this.$router.push({ 
                    name: '2', 
                    params: {
                        name: this.msg
                    } 
                });
            }
        }
    }
};
</script>